:root {
    --controlsWidth:500px;
}
.controlPane{
    background-color: var(--bgGrey);
    border-color: var(--highlightCol);
    border-width: 2px;
    border-style: solid;
    /* display: flex; */
    position: fixed;
    top: calc(var(--navBarHeight) + 4px);
    right: 0px;
    z-index: 999;
    height: 200px;
    width: var(--controlsWidth);
    font-size: 40px;
    color: aliceblue;
    /* align-content: space-around; */
    /* justify-content: left; */
    
}

.contolsEditText {
    padding-top: 10px;
    font-size: 20px;
    background-color: var(--bgGrey);
    color:var(--highlightCol);
    text-align: center;
    align-content: center;
}

.pages{
    clear:both;
    display: block;
    
    /* position: relative;
    width: 100%; */
}

.pageButton{
    background-color: var(--bgGrey);
    border-color: var(--highlightCol);
    border-width: 2px;
    border-style: solid;
    border-radius: 5px;

    display: inline-block;
    position: fixed;
    top: calc(var(--navBarHeight) + 215px);
    height: 20px;
    width: 54px;
    margin: 0.5vw;

    color: var(--highlightCol);
    font-size: small;
    text-align: center;
}

.pageButton:hover{
    background-color:rgb(81, 133, 134);
}
