@font-face {
    font-family: 'arcade';
    src: local('arcade'), url(../resources/fonts/karmatic-arcade/ka1.ttf) format('truetype');
  }


:root {
    --bgCol:rgb(14, 6, 59);
    --bgGrey: #212529ee;
    --bgGrey2: #35383bee;
    --textCol:rgb(255, 255, 255);
    --highlightCol: rgb(64, 211, 174);
    --unHighlightCol: rgb(22, 77, 63);
    --accentCol: rgb(204, 27, 195);

    --navBarHeight:50px;
}

.navbar {
    background-color: var(--bgGrey);
    border-bottom-color: var(--highlightCol);
    border-bottom-width: 2px;
    border-bottom-style: solid;
    display: flex;
    position: fixed;
    z-index: 998;
    height: calc(var(--navBarHeight));
    width: 100%;
    font-size: 40px;
    color: aliceblue;
    align-content: space-around;
    justify-content: left;
}

.menuIcon {
    padding-top: 1px;
}

.menuIcon:hover{
    color: var(--accentCol);
}

.menu{
    background-color:var(--bgGrey);
    width: 440px;
    height: 100vh; 
    display: grid;
    grid-template-columns: 100px 100px 100px 100px;
    grid-template-rows: 50px 50px 50px 50px;
    grid-gap: 10px;
    justify-content: left;
    /* overflow: hidden; */
    position: fixed; 
    top: calc(var(--navBarHeight) + 2px);
    padding-top: 20px;
    transition: 500ms;
    padding-left: 10px;
    font-size: 10px;   
    color: var(--textColor);
    z-index: 999;
}

.menu_show {    
    left: 0px;
}

.menu_hide {
    left: -450px;
}

.menuTitle {
    grid-column: 1/ span 4;
    color: var(--highlightCol);
    position: relative;
    font-family: Arial;
    font-size: 20px;
    width: auto;
    height: auto;
    padding-bottom: 20px;
}

.menuSubtitle {
    font-size: medium;
    color: var(--textCol);
    padding-bottom: 10px;
}


.menuTabs {
    background-color: #00000000;
    border: 1px solid var(--textCol);
    color: var(--textCol);
    font-weight: bold;
    position: relative;
    border-radius: 3px;
    width: 100px;
    min-width: 100px;
    height: 32px;
    top: 0px;
    font-size: small;
    /* padding: 0px; */
}

.menuTab1 {
    grid-column:"1/ span 1";
}
.menuTab2 {
    grid-column:"2/ span 1";
}
.menuTab3 {
    grid-column:"3/ span 1";
}

input[type="file"] {
    display: none;
}

.menuButton {
    background-color: #00000000;
    border: 1px solid var(--textCol);
    color: var(--textCol);
    font-weight: bold;
    position: relative;
    border-radius: 3px;
    width: 100px;
    min-width: 100px;
    height: 32px;
    top: 0px;
    font-size: small;
    padding: 0px;
    text-align: center;
}
.menuButton:hover
{
    background-color: var(--highlightCol);
    color: black;
}


