.nodeStyle{
    color: #ffffff;
    background: '#555';
    border: 2px solid var(--highlightCol);
    border-radius: 5px;
    text-align: center;
    padding: 10px;
    width: 150px;
    height:auto;
}

.nodeStyle.selected{
    border: 2px solid var(--accentCol);
}

.portStyle{
    width:'50px';
    height:'10px';
    background: '#555';
}

.objSepStyle{
    border-top: 2px solid var(--highlightCol);
    width: 170px;
    margin-left: -10px;
    border-bottom: none;
    border-left: none;
    border-right: none;
}
.objSepStyle.selected{
    border-top: 2px solid var(--accentCol);
}

.ObjUiTitle{
    color: #ffffff;
    font-size:'x-large';
    
}

.ObjUiParamName{
    display:inline-block;
    color: #ffffff;
    font-size: small;
    width: 60px;
}

.ObjUiParamVal{
    display:inline-block;
    width: 80px;
    color: #ffffff;
    font-size: small;
}